import classes from './Banner.module.scss'
import { useState } from 'react'
import { IBanner } from '../../models/IBanner'
import installmentIcon from '../../assets/icons/installment.svg'
import operatorIcon from '../../assets/icons/operator.svg'
import tradeIcon from '../../assets/icons/trade.svg'
import BannerList from './BannerList/BannerList'

function Banner() {
  const [banners] = useState<IBanner[]>([
    {title: '', text: ' ', color: 'blue', svg: installmentIcon, textLite: ' '},
    {
      title: '',
      text: '   ',
      color: 'orange',
      svg: operatorIcon,
      textLite: '  '
    },
    {title: 'Trade-in', text: '  ', color: 'green', svg: tradeIcon, textLite: '  '},
  ])
  return (
    <section className={classes.Banner}>
      <h2 className={classes.Title}></h2>

      <BannerList bannerList={banners}/>
    </section>
  )
}

export default Banner